import { useState } from "react";

import { Link } from "react-router-dom";

const Post = ({ post }) => {
  const { id, title, img, description, isLiked } = post;
  const [liked, setLiked] = useState(isLiked);

  const likeHandler = () => setLiked(!liked);

  return (
    <li className="post">
      <h2>{title}</h2>
      <Link to={`/post-page/${id}`}>
        <img src={img} alt={title} />
      </Link>
      <div className="post-detail">
        {liked ? (
          <i onClick={likeHandler} className="fa fa-heart liked"></i>
        ) : (
          <i onClick={likeHandler} className="fa fa-heart-o"></i>
        )}
        <i className="fa fa-comment-o"></i>
      </div>
      <p>{description}</p>
    </li>
  );
};

export default Post;
