import "./App.css";
import Posts from "./components/Posts";
import PagePost from "./components/PagePost";
import data from "./data.js";

import { BrowserRouter, Route, Switch } from "react-router-dom";

const App = () => {
  return (
    <BrowserRouter>
      <div className="container">
        <Switch>
          <Route path="/" exact>
            <Posts data={data} />
          </Route>
          <Route
            path="/post-page/:id"
            render={(props) => <PagePost {...props} data={data} />}
          />
        </Switch>
      </div>
    </BrowserRouter>
  );
};

export default App;
