import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";

const PagePost = (props) => {
  const inputNameRef = useRef(null);
  const postId = props.match.params.id;
  const post = props.data.find((post) => post.id === postId);

  const [name, setName] = useState("");
  const [comment, setComment] = useState("");
  const [postComments, setPostComments] = useState(post.comments);

  const submitForm = (e) => {
    e.preventDefault();
    if (name.trim() !== "" && comment !== "") {
      const allComments = [...postComments];
      allComments.push({ name, comment });
      setPostComments(allComments);
      setName("");
      setComment("");
      inputNameRef.current.focus();
    }
  };

  return (
    <div>
      <Link to="/">Back to home</Link>
      <h1>this is post page</h1>
      <div className="single-post-wrapper">
        <img src={`../${post.img}`} alt="" />
        <div className="comments-wrapper">
          <ul>
            {postComments.map((cmt, i) => (
              <li key={i}>
                <strong>{cmt.name}</strong>
                <p>{cmt.comment}</p>
              </li>
            ))}
          </ul>
          <form action="" onSubmit={submitForm}>
            <input
              ref={inputNameRef}
              value={name}
              onChange={(e) => setName(e.target.value)}
              type="text"
              placeholder="username"
            />
            <input
              value={comment}
              onChange={(e) => setComment(e.target.value)}
              type="text"
              placeholder="new comment"
            />
            <button>Add Comment</button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default PagePost;
